@import url("https://use.typekit.net/jmk3xov.css");

canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 50%;
  background: var(--dark-bg);
  display: block;
}
@font-face {
  font-family: "Courier";
  src: url("./public/font/CourierPrime-Regular.ttf")
  format("ttf");
  font-style: normal;
  font-weight: 400;
}
main {
  font-family: "Courier";
  width: 100vw;
  color: white;
  z-index: 99;
  position: absolute;
  width: 100%;
  margin: 0px auto;
  padding: 120px 0px;
  
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

header {
  font-family: "Courier";
  border-radius: 25px;
  border: 2px solid #000000;
  background: var(--dark-bg);
  grid-column: 2 / span 6;
  font-size: 2.5rem;
  padding: 2rem;
  margin-bottom:120px;
}
section {
  border-radius: 25px;
  grid-column: 2 / 8;
  padding: 0.75rem;
  background: var(--dark-bg);
  font-size: 1.25rem;
  line-height: 1.5;
  margin-bottom: var(--spacing);
  margin-top: -150px;
}
:root {
  --dark-bg: rgba(15, 15, 15, 1);
  --spacing: 350px;
  background: var(--dark-bg);
  font-family: "Courier";
  font-weight: 400;
  font-style: normal;
}
blockquote {
  margin: 0;
  padding: 0.25px;
  grid-column: 2 / span 9;
  
  margin-bottom: 200px;

}
img {
  border-radius: 25px;
  border: 2px solid #000000;
  
}
blockquote p {
  color: black;
  border-radius: 25px;
  padding: 10px;
  border: 2px solid #000000;
  background-color: white;
  background-color: rgba(255, 255, 255, 0.5);
  font-size: 2rem;
  display: inline;
  line-height: 1;
  
}

.left {
  grid-column: 6 / 12;
}